const reducer = (state = initialState, action) => {
    
    const {type, payload} = action;

    switch (type) {
        case 'LOGIN': 
            return {
                ...state,
                loggedIn: true
            }
        case 'LOGOUT': 
            return {
                ...state,
                loggedIn: false
            }
        default: 
            return state
    }
}

export default reducer;